import React, {Component, useContext, useState} from 'react';
import {connect} from 'react-redux';
import HeroComponents from "../../../../../../../components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "../../../../../../../components/permission-components/button.permission";
import ContainerTailwind from "../../../../../../../components/layout-components/ContainerTailwind";
import {Button, Card,message, Col, Divider, Form, Menu, Modal, Popconfirm, Table} from "antd";
import {
    EyeOutlined,
    ControlFilled,
    FilterOutlined,
    PlusCircleOutlined,
    PlayCircleFilled,
    PauseCircleFilled
} from "@ant-design/icons";
import EllipsisDropdown from "../../../../../../../components/shared-components/EllipsisDropdown";
import Flex from "../../../../../../../components/shared-components/Flex";
import {Link} from "react-router-dom";
import {GetPath} from "../../../../../../../utils";
import LayoutPermission from "../../../../../../../components/permission-components/layout.permission";
import AudioIpAddressForm from "../components/audio-ip-address.form";
import {MusicContext} from "../../../../../../../components/app-component/music-app";
import {__AnOpenPlayer} from "../../../../../../../redux/actions/Announcement";

function mapStateToProps(state) {
    return {
        state
    };
}

function Index({__AnOpenPlayer,...props}) {

    const  [ data, setData ] = useState([
        {
            id:1,
        },
        {
            id:1,
        },

    ])
    const [visible,setVisible] = useState(false);
    const [ loading, setLoading ] = useState(false)
    const [ params, setParams ] = useState({
        page:1,
        limit:10
    })
    const [loadings, setLoadings] = useState([]);
    const [visibilities,setVisibilities ] = useState([]);
    const [callers,setCaller] = useState([]);

    const enterLoading = (index,...args) => {
        setLoadings((prevLoadings) => {
            const newLoadings = [...prevLoadings];
            newLoadings[index] = true;
            return newLoadings;
        });
        setTimeout(() => {
            setLoadings((prevLoadings) => {
                const newLoadings = [...prevLoadings];
                newLoadings[index] = false;
                return newLoadings;
            });
            if(Array.isArray(args) && args.length > 0){
                args.map((item)=> {
                    if(typeof(item) === 'function'){
                        item();
                        return item();
                    }
                    return item
                })
            }
        }, 6000);

    };


    const onGenerates = (_,index)=>{
        let onvisible = setVisibilities((prevLoadings) => {
            const newLoadings = [...prevLoadings];
            newLoadings[index] = true;
            return newLoadings;
        });
        let onvisiblefalse = setTimeout(()=> {
            setVisibilities((prevLoadings) => {
                const newLoadings = [...prevLoadings];
                newLoadings[index] = false;
                return newLoadings;
            });
        },3000)

        function generate(){
            return new Promise((resolve)=> {
                resolve(
                    enterLoading(index,onvisible,onvisiblefalse)
                )
            });
        }
        return new Promise((resolve)=> setTimeout(()=> resolve(generate),3000))
    }





    const columns = [
        {
            dataIndex:"id",
            title:"No",
            width:70,
            render: (_,val,index)=> index + 1
        },
        {
            dataIndex:"id",
            title:"Type",
            render: (_,val,index)=> "TO-BOARDING-GATE"
        },
        {
            dataIndex:"id",
            title:"Airlines",
            render: (_,val,index)=> "LION-AIR"
        },
        {
            dataIndex:"id",
            title:"Code",
            render: (_,val,index)=> "LION-AIR"
        },
        {
            dataIndex:"id",
            title:"To",
            render: (_,val,index)=> "LION-AIR"
        },
        {
            dataIndex:"id",
            title:"Gate Number",
            render: (_,val,index)=> "LION-AIR"
        },
        {
            dataIndex:"id",
            title:"Floor",
            render: (_,val,index)=> "LION-AIR"
        },
        {
            render:(_,val,index)=> {
                return (
                    <div className={'text-right mk-flex !mk-justify-end !mk-gap-2'}>
                        <Popconfirm
                            title="Generate Announcement"
                            description={'Lorem ipsum dolor sit amet lorem ipsum dolor sit amet'}
                            onConfirm={()=> {
                                return onGenerates(_,index)
                            }}
                            // onOpenChange={() => console.log('open change')}
                            okButtonProps={{
                                loading: visibilities[index],
                            }}
                        >
                            <Button
                                icon={<ControlFilled style={{fontSize:16}} />}
                                type="primary" >
                                Generate
                            </Button>
                        </Popconfirm>
                        <ButtonPermission
                            // permission={['public', 'admin','guest',null,'']}
                            theme={{
                                type: "notification",
                                property: {
                                    message: "Access Denied"
                                }
                            }}
                            config={{
                                icon: <PlayCircleFilled />,
                                type: "primary",
                                danger: false,
                                ghost: false,
                                shape:"circle",
                                size: "small",
                                onClick: ()=> {
                                    __AnOpenPlayer({
                                        isType:"SELECTED"
                                    })
                                }
                            }}/>
                        <EllipsisDropdown menu={
                            <Menu>
                                <Menu.Item>
                                    <Flex alignItems="center">
                                        <EyeOutlined/>
                                        <span className="ml-2">View Details</span>
                                    </Flex>
                                    <Link to={GetPath.detail('city',_?.id)}/>
                                </Menu.Item>
                                <LayoutPermission
                                    extra={<></>}
                                    permission={['public']}>
                                    <Menu.Item>
                                        <Flex alignItems="center">
                                            <PlusCircleOutlined/>
                                            <span className="ml-2">Edit</span>
                                        </Flex>
                                    </Menu.Item>
                                </LayoutPermission>

                                <LayoutPermission
                                    extra={<></>}
                                    permission={['admin']}>
                                    <Divider className={'my-0'}/>
                                    <Menu.Item>
                                        <Flex alignItems="center">
                                            <PlusCircleOutlined/>
                                            <span className="ml-2">Delete</span>
                                        </Flex>
                                    </Menu.Item>
                                </LayoutPermission>
                            </Menu>
                        }/>
                    </div>
                )
            }
        }





    ]


    const [ form ] = Form.useForm();

    const onFinish = ()=> {
        form.validateFields()
            .then((value)=> {
                if(callers && Array.isArray(callers) && callers.length > 0) {

                    callers.map((item)=> item());
                    setCaller([])
                }
                form.resetFields();
                setVisible(false)
            })
            .catch((err)=> {
                message.error(`Error: Validatae Form`)
            })
    }
    return (
        <React.Fragment>
            <Modal
                title={'Halo'}
                visible={visible}
                onCancel={()=> setVisible(false)}
                closable={false}
                footer={[
                    <ButtonPermission
                        // permission={['admin','']}
                        theme={{
                            type:"notification",
                            property:{
                                message:"Access Denied"
                            }
                        }}
                        config={{
                            type:"primary",
                            size:"small",
                            ghost:true,
                            onClick: function(){
                                setVisible(false)
                            }
                        }}>
                        Close
                    </ButtonPermission>,
                    <ButtonPermission
                        permission={['guest','admin']}
                        theme={{
                            type:"notification",
                            property:{
                                message:"Access Denied"
                            }
                        }}
                        config={{
                            type:"primary",
                            size:"small",
                            ghost:false,
                            onClick:onFinish
                        }}>
                        Play Audio
                    </ButtonPermission>
                ]}
            >
                <AudioIpAddressForm
                    form={form}
                />
            </Modal>
            <HeroComponents
                loading={false}
                name={"Announcement"}
                positionName={'General | Audio Announcement'}
                thumbnail={null}
                extra={<>
                    <ButtonPermission
                        permission={['admin']}
                        theme={{
                            type: "disabled",
                            property: {
                                message: "Access Denied"
                            }
                        }}
                        config={{type: "primary", size: "small"}}>
                        Create
                    </ButtonPermission>
                </>}
            />
            <div style={{marginTop:90}}>
                <ContainerTailwind>
                    <Card extra={
                        <div className={'text-right`'}>
                            <ButtonPermission
                                permission={['admin']}
                                theme={{
                                    type: "disabled",
                                    property: {
                                        message: "Access Denied"
                                    }
                                }}
                                config={{
                                    type: "primary",
                                    size: "small",
                                    ghost:true,
                                    icon: <FilterOutlined/>
                            }}>
                                Filter
                            </ButtonPermission>
                        </div>
                    }>
                        <Table
                            loading={loading}
                            dataSource={data}
                            columns={columns}
                        />
                    </Card>
                </ContainerTailwind>
            </div>
        </React.Fragment>
    );
}
Index.contextType = MusicContext
export default connect(
    mapStateToProps,{__AnOpenPlayer}
)(Index);